<template>
  <el-container style="height: 100%; border: 1px solid #eee" class="wrapper">
    <el-aside width="200px" style="background-color: rgb(238, 241, 246)">
      <!--<el-tree :data="data" :props="defaultProps" @node-click="handleNodeClick"></el-tree>-->
      <el-menu :default-openeds="['1', '3', '4']">
        <el-submenu index="1">
          <template slot="title"><i class="el-icon-message"></i>运行管理</template>
          <router-link :to="{ name:'Txn'}" tag="li" replace>
            <el-menu-item index="1-1">交易模型</el-menu-item>
          </router-link>
          <router-link :to="{ name:'List'}" tag="li" replace>
            <el-menu-item index="1-2">名单管理</el-menu-item>
          </router-link>
        </el-submenu>
        <!--<el-submenu index="2">-->
          <!--<template slot="title"><i class="el-icon-menu"></i>综合查询</template>-->
          <!--<el-menu-item index="2-1">交易事件查询</el-menu-item>-->
          <!--<el-menu-item index="2-2">报警事件查询</el-menu-item>-->
        <!--</el-submenu>-->
        <el-submenu index="3">
          <template slot="title"><i class="el-icon-setting"></i>权限管理</template>
          <router-link :to="{ name:'User'}" tag="li" replace>
            <el-menu-item index="3-1">操作员管理</el-menu-item>
          </router-link>
          <router-link :to="{ name:'Role'}" tag="li" replace>
            <el-menu-item index="3-2">角色管理</el-menu-item>
          </router-link>
          <router-link :to="{ name:'Function'}" tag="li" replace>
            <el-menu-item index="3-3">功能管理</el-menu-item>
          </router-link>
        </el-submenu>
        <el-submenu index="4">
          <template slot="title"><i class="el-icon-setting"></i>系统管理</template>
          <router-link :to="{ name:'Codedict'}" tag="li" replace>
            <el-menu-item index="4-1">数据字典管理</el-menu-item>
          </router-link>
          <router-link :to="{ name:'Ipaddr'}" tag="li" replace>
            <el-menu-item index="4-3">IP地址维护</el-menu-item>
          </router-link>
          <router-link :to="{ name:'AuthCenter'}" tag="li" replace>
            <el-menu-item index="4-4">授权中心</el-menu-item>
          </router-link>
          <router-link :to="{ name:'Log'}" tag="li" replace>
            <el-menu-item index="4-4">操作日志管理</el-menu-item>
          </router-link>
        </el-submenu>

      </el-menu>
    </el-aside>
    <el-container>
      <el-header style="text-align: right; font-size: 12px">
        <el-dropdown>
          <i class="el-icon-setting" style="margin-right: 15px"></i>
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item>查看</el-dropdown-item>
            <el-dropdown-item>新增</el-dropdown-item>
            <el-dropdown-item>删除</el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
        <span>王小虎</span>
      </el-header>

      <el-main>
        <!--用router-view渲染视图-->
        <router-view/>
      </el-main>
    </el-container>
  </el-container>
</template>

<script>
  export default {
    data() {
      return {
        data: [{
          label: '一级 1',
          children: [{
            label: '二级 1-1',
            children: [{
              label: '三级 1-1-1',
              name: '/User'
            }]
          }]
        }, {
          label: '一级 2',
          children: [{
            label: '二级 2-1',
            children: [{
              label: '三级 2-1-1',
              name: '/Role'
            }]
          }, {
            label: '二级 2-2',
            children: [{
              label: '三级 2-2-1'
            }]
          }]
        }, {
          label: '一级 3',
          children: [{
            label: '二级 3-1',
            children: [{
              label: '三级 3-1-1'
            }]
          }, {
            label: '二级 3-2',
            children: [{
              label: '三级 3-2-1'
            }]
          }]
        }],
        defaultProps: {
          children: 'children',
          label: 'label'
        }
      };
    },
    methods: {
      handleNodeClick(data) {
        console.log(data);
        if (!data.children || data.children.length === 0){
          this.$router.push(data.name);
        }
      }
    }
  };
</script>

<style>
  .el-header {
    background-color: #B3C0D1;
    color: #333;
    line-height: 60px;
  }

  .el-aside {
    color: #333;
  }
</style>



